@import url(https://fonts.googleapis.com/css2?family=Saira+Condensed:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
@font-face {
  font-family: 'HelveticaNeue-Local';
  src: local('HelveticaNeue-Local'), url(/klukva-translator/static/media/HelveticaNeue.429952c9.ttf) format('truetype');
}

@font-face {
  font-family: 'HelveticaNeue-CondensedBlack-Local';
  src: local('HelveticaNeue-CondensedBlack-Local'), url(/klukva-translator/static/media/HelveticaNeue-CondensedBlack.ca891c1d.ttf) format('truetype');
}

@font-face {
  font-family: 'HelveticaNeue-Light-Local';
  src: local('HelveticaNeue-Light-Local'), url(/klukva-translator/static/media/HelveticaNeue-Light.8c1c8f8e.ttf) format('truetype');
}

@font-face {
  font-family: 'HelveticaNeue-Medium-Local';
  src: local('HelveticaNeue-Medium-Local'), url(/klukva-translator/static/media/HelveticaNeue-Medium.b6708730.ttf) format('truetype');
}


body {
    margin: 0;
    padding: 0;
    background: #202839;
}


.App {

}

.Background {
    background: #c3375e;
    width: 64vw;
    height: 100vh;
}

.Logo {
    position: absolute;
    top: 64px;
    bottom: 64px;
    left: 64px;
    height: 48px;
}

.Header {
    position: absolute;
    top: 64px;
    bottom: 64px;
    left: 64px;
    right: 50vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
}

.Header-Content {
    margin-right: 64px;
}

h1 {
    margin: 0;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-CondensedBlack-Local', 'HelveticaNeue-CondensedBlack', 'Helvetica Neue Condensed Black', 'Helvetica Neue', 'Saira Condensed', Helvetica;
    font-size: 64px;
    font-weight: 900;
    line-height: 60px;
    font-stretch: condensed;
    text-transform: uppercase;
}

p {
    margin: 0;
    margin-top: 24px;
    color: #202839;
    font-family: 'HelveticaNeue-Light-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 20px;
    line-height: 30px;
    font-weight: 300;
    max-width: 800px;
}

a {
    margin: 0;
    margin-top: 24px;
    color: #202839;
    font-family: 'HelveticaNeue-Light-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 20px;
    line-height: 30px;
    font-weight: 300;
    max-width: 800px;
    text-decoration: none;
}

@media only screen and (max-width: 768px) {
    .Header {
        display: none;
    }

    .Logo {
        top: 24px;
        bottom: 24px;
        left: 24px;
    }

    .Background {
        height: 36vh;
        width: 100vw;
    }
}

.Board {
    position: absolute;
    top: 64px;
    bottom: 64px;
    right: 64px;
    left: 50vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.Board-Panel {
    margin: auto;
    width: 100%;
    height: 100%;;
    display: -webkit-flex;;
    display: flex;
    -webkit-flex-grow: 0.5;
            flex-grow: 0.5;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: stretch;
            align-items: stretch;
}

.Board-Space {
    height: 48px;
}

.Board-Panel-Editor {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    border-radius: 12px 12px 0 0;
    background: #ffffff;
}

.Board-Panel-Separator {
    height: 1px;
    background: #e9ecf1;
}

.Board-Panel-Toolbar {
    height: 64px;
    display: -webkit-flex;
    display: flex;
    border-radius: 0 0 12px 12px;
    background: #ffffff;
}

.Board-Panel-Toolbar-Button {

}

textarea {
    width: 100%;
    border: 0;
    outline: none;
    padding: 24px;
    margin: 0;
    background: none;
    resize: none;
    color: #50596a;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
}

textarea::-webkit-input-placeholder {
    color: #202839;
    opacity: 0.38;
}

textarea:-ms-input-placeholder {
    color: #202839;
    opacity: 0.38;
}

textarea::placeholder {
    color: #202839;
    opacity: 0.38;
}

.Board-Panel-Toolbar {
    min-height: 64px;
}

.Board-Panel-ButtonGroup {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: right;
            align-items: right;
    margin-left: auto;
    margin-right: 8px;
}

.Board-Panel-Button {
    margin: 8px;
    margin-left: auto;
    padding: 0;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 48px;
    background: transparent;
    transition: 0.48s;
}

.Board-Panel-Button:hover {
    background: #f4f4f6;
    transition: 0.32s;
}

.Board-Panel-Button:active {
    background: #ececef;
    transition: 0.08s;
}

.Icon {
    margin: 0;
    width: 16px;
    height: 16px;
    padding: 16px;
}

.Board-Panel-Switcher {
    display: -webkit-flex;
    display: flex;
    margin-left: 28px;
}

.Board-Panel-Switcher-Label {
    margin: auto;
    margin-right: 8px;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
}

.Board-Panel-Switcher-Button {
    border: none;
    outline: none;
    background: transparent;
    margin: auto;
    padding: 0;
    cursor: pointer;
    color: #c3375e;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
}

.Board-Panel-Counter {
    display: -webkit-flex;
    display: flex;
    margin-left: 28px;
}

.Board-Panel-Counter-Label {
    margin: auto;
    margin-right: 8px;
    padding: 0;
    color: #202839;
    font-family: 'HelveticaNeue-Local', 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 12px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
}

.Board-Tooltip {
    padding: 4px 12px 4px 12px !important;
    color: #ffffff !important;
    font-family: 'Helvetica Neue', 'Roboto', Helvetica;
    font-size: 12px !important;
    font-weight: 300;
    line-height: 24px;
    text-transform: uppercase;
}

@media only screen and (max-width: 768px) {
    .Board {
        top: 96px;
        bottom: 24px;
        left: 24px;
        right: 24px;
    }
}

